<template>
	<aside class="aside__top">
		<!--     <i v-if="isSidebarNavCollapse"
            class="el-icon-s-fold"
            @click="toggleNavCollapse"
        >
        </i>
		<i v-else
		     class="el-icon-s-unfold nav-icon"
		     @click="toggleNavCollapse"
		 >
		 </i> -->
		<el-breadcrumb separator="/">
			<transition-group name="breadcrumb">
				<!-- 防止面包屑导航出现 首页/首页， v-if="route.name!='home'" -->
				<template v-for="(route,i) in crumbList">
					<el-breadcrumb-item :key="route.name" :to="{name:route.name}" v-if="route.name!='home' && route.meta.title!='首页'"
					 :class="{'is-last-link':i==crumbList.length-1}">
						{{route.meta.title}}
					</el-breadcrumb-item>
				</template>
			</transition-group>
		</el-breadcrumb>
		<div class="aside__top--right">
			<div class="user-msg">
				<!-- <img class="user-img" :src="avatar" alt=""> -->
				<span class="user-name">{{username}}</span>
				<!--      <el-dropdown trigger="click" placement="top">
                    <span class="el-dropdown-link">
                        <i class="el-icon-arrow-down el-icon--right"></i>
                    </span>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item>修改密码</el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown> -->
			</div>
			<div class="quit-system">
				<el-button size="mini" type="primary" @click="logout">退出</el-button>
			</div>
		</div>
	</aside>
</template>

<script>
	import {
		mapState
	} from 'vuex'
	import API from '@/api/modules/login.js'

	export default {
		data() {
			return {
				username: localStorage.getItem('user')
			}
		},
		computed: {
			...mapState(['isSidebarNavCollapse', 'crumbList'])
		},
		methods: {
			toggleNavCollapse() {
				this.$store.commit('toggleNavCollapse')
			},
			logout() {
				/* 防止切换角色时addRoutes重复添加路由导致出现警告 */
				API.logout().then(res => {
					this.$router.replace('/login')
					this.$store.commit('LOGIN_OUT')
				})
				// window.location.reload()
			}
		}
	}
</script>

<style lang="scss" scoped>
	.aside__top {
		border-bottom: 1px solid #e5e5e5;
		height: 50px;
		line-height: 50px;
		position: fixed;
		left: 200px;
		top: 0;
		right: 0;
		background: #303133;
		z-index: 1000;
		transition: left 0.25s;

		.toggleNavCollapse {
			display: inline-block;
			margin-left: 8px;
			padding: 0 10px;
			font-size: 26px;
			vertical-align: middle;
			color: #333;
			cursor: pointer;
			transition: all 0.5s;

			&.active {
				transform: rotate(90deg);
			}
		}

		.aside__top--right {
			position: absolute;
			right: 10px;
			top: -1px;
			bottom: 0px;

			>div {
				position: relative;
				display: inline-block;
				text-align: center;
				vertical-align: middle;
				margin-left: 10px;
				padding: 0 15px;
				cursor: pointer;

				&:hover::after {
					transform-origin: 0 0;
					transform: scaleX(1);
				}

				&:first-child:before {
					border: none;
				}

				&::after {
					content: '';
					position: absolute;
					left: 0;
					bottom: 0;
					width: 100%;
					height: 2px;
					background: #ef4747;
					transform: scaleX(0);
					transform-origin: right 0;
					transition: transform 0.5s;
				}

				&::before {
					content: '';
					position: absolute;
					height: 20px;
					top: 50%;
					left: -8px;
					margin-top: -10px;
					border-left: 1px solid #ccc;
				}

				&.email {
					i {
						position: absolute;
						left: 18px;
						top: -12px;
						border-radius: 20px;
						background: red;
						color: #fff;
						text-align: center;
						font-size: 12px;
						line-height: 1.5;
						min-width: 20px;
						min-height: 20px;
					}
				}

				&.user-msg {
					.user-img {
						width: 34px;
						height: 34px;
						border-radius: 50%;
						vertical-align: middle;
					}

					.user-name {
						color: #758eb5;
						padding: 0 4px;
						font-size: 20px;
					}
				}

				.iconfont {
					position: relative;
					font-size: 24px;
					color: #758eb5;
				}
			}
		}
	}

	.el-breadcrumb {
		display: inline-block;
		vertical-align: middle;
		font-size: 14px;
		margin-left: 5px;

		.el-breadcrumb__inner {
			&.is-link {
				display: inline-block;
				font-weight: normal;
				color: #000 !important;
			}
		}

		.is-last-link .is-link {
			font-weight: normal;
			color: #000 !important;
		}
	}

	.breadcrumb-enter,
	.breadcrumb-leave-active {
		opacity: 0;
		transform: translateX(20px);
	}

	.breadcrumb-enter-active,
	.breadcrumb-leave-active {
		transition: all 0.6s;
	}

	.breadcrumb-leave-active {
		position: absolute;
	}
</style>
