<template>
	<div class="container">
		<template v-for="item in menuList">
			<el-submenu :index="item.name" :key="item.name" v-if="item.children && item.children.length > 0">
				<template slot="title">
					<i :class="item.meta.icon"></i>
					<span>{{ item.title }}</span>
				</template>
				<el-menu-item-group>
					<my-nav :menuList='item.children'></my-nav>
				</el-menu-item-group>
			</el-submenu>
			<el-menu-item :index="item.name" :key="item.name" @click="gotoRoute(item.name)" v-else>
				<i :class="item.meta.icon"></i>
				<span slot='title'>{{ item.title }}</span>
			</el-menu-item>
		</template>
	</div>
</template>

<script>
	import {
		mapState
	} from 'vuex'
	export default {
		name: 'my-nav',
		props: {
			menuList: {
				type: Array,
				default: () => {
					return []
				}
			}
		},
		data() {
			return {
				openedTab: []
			}
		},
		computed: {
			...mapState('permission', ['sidebarMenu'])
		},
		methods: {
			gotoRoute(name) {
				this.$router.push({name}); 
			}
		}
	}
</script>

